import React, { useEffect } from "react";
// import NavScrollExample from "../../components/Navbar";
import { useParams } from "react-router-dom";
// import Sidebar from "../../components/sidebar/Sidebar";
import { Link } from "react-router-dom";
import {
  CCard,
  CCardBody,
  CCol,
  CCardHeader,
  CImage,
  CRow,
  CCardTitle,
  CButton,
  CCardSubtitle,
} from "@coreui/react"; 
import axios from "axios";
import { useState } from "react";
// import { Language } from "@mui/icons-material";

const ViewLawyer = () => {
  const { id } = useParams();
  const token =
    typeof window !== "undefined" ? localStorage.getItem("status") : null;
 

  const [mydata, setMydata] = useState([]);

  // const [chatHistory, setChatHistory] = useState([]);

  useEffect(() => {
    axios
      .get("lawyerDetailforAdmin/" + id, {
        headers: {
          Accept: "application/json",
          Authorization: "Bearer " + token,
        },
      })
      .then((res) => {
        // res.data.status == 200 ?   setMydata(res.data.data):  setMydata([])
        setMydata(res.data.data);
        console.log("single_profile", res.data.data);
        
      });
  }, [id, token]);

  

  return (
    <>
      {/* <NavScrollExample /> */}
      <div className="container-fluid ">
        <div className="row">
          <div className="col-md-2  p-0">
            <div className="sidebar-open">{/* <Sidebar /> */}</div>
          </div>
          <div className="col-md-12 pl-60">
            <div className="row mt-4 px-2 ">
              <div className="px-2">
                <CCol md={12}>
                  <CCard className="mb-4">
                    <CCardHeader>
                      <strong>View Lawyer Details</strong>
                    </CCardHeader>
                    <CCardBody>
                      <CCol md={12} className="d-flex my-3">
                        <CCol sm={1}> </CCol>{" "}
                        <CCol sm={3}>
                          <span style={{ marginRight: "15px" }}>
                            <strong>Profile :</strong>
                          </span>{" "}
                        </CCol>{" "}
                        <CCol sm={7}>
                          {mydata?.profile_img == "" ? (
                            <span style={{ color: "red" }}>
                              No Profile Image
                            </span>
                          ) : (
                            <Link to={mydata?.profile_img} target="_blank">
                              {" "}
                              <CImage
                                // align="center"
                                width={75}
                                rounded
                                thumbnail
                                orientation="top"
                                src={mydata?.profile_img}
                              />{" "}
                            </Link>
                          )}
                        </CCol>
                      </CCol>

                      <CCol md={12} className="d-flex my-3">
                        <CCol sm={1}> </CCol>{" "}
                        <CCol sm={3}>
                          <span style={{ marginRight: "15px" }}>
                            <strong>Name :</strong>
                          </span>{" "}
                        </CCol>{" "}
                        <CCol sm={7}>
                          <span className="text-capital">
                            {mydata?.first_name + " " + mydata?.last_name}
                          </span>
                        </CCol>
                      </CCol>

                      <CCol md={12} className="d-flex my-3">
                        <CCol sm={1}> </CCol>{" "}
                        <CCol sm={3}>
                          <span style={{ marginRight: "15px" }}>
                            <strong>User Name :</strong>
                          </span>{" "}
                        </CCol>{" "}
                        <CCol sm={7}>
                          <span className="text-capital">
                            {mydata?.user_name}
                          </span>
                        </CCol>
                      </CCol>

                      <CCol md={12} className="d-flex my-3">
                        <CCol sm={1}> </CCol>{" "}
                        <CCol sm={3}>
                          <span style={{ marginRight: "15px" }}>
                            <strong>Mobile number:</strong>
                          </span>{" "}
                        </CCol>{" "}
                        <CCol sm={7}>{mydata?.phone}</CCol>
                      </CCol>

                      <CCol md={12} className="d-flex my-3">
                        <CCol sm={1}> </CCol>{" "}
                        <CCol sm={3}>
                          <span style={{ marginRight: "15px" }}>
                            <strong>Gender :</strong>
                          </span>{" "}
                        </CCol>{" "}
                        <CCol sm={7}>
                          {" "}
                          {mydata?.gender == "1"
                            ? "Male"
                            : mydata?.gender == "2"
                            ? "Female"
                            : "Other"}
                        </CCol>
                      </CCol>

                      <CCol md={12} className="d-flex my-3">
                        <CCol sm={1}> </CCol>{" "}
                        <CCol sm={3}>
                          <span style={{ marginRight: "15px" }}>
                            <strong>Education Details :</strong>
                          </span>{" "}
                        </CCol>{" "}
                        <CCol sm={7}> {mydata?.education_details}</CCol>
                      </CCol>

                      <CCol md={12} className="d-flex my-3">
                        <CCol sm={1}> </CCol>{" "}
                        <CCol sm={3}>
                          <span style={{ marginRight: "15px" }}>
                            <strong>Practice Areas :</strong>
                          </span>{" "}
                        </CCol>{" "}
                        <CCol sm={7}>
                          <ol className="px-3" style={{ padding: "0px" }}>
                            {mydata?.practice_area?.map((items, index) => {
                              return <li key={index}>{items}</li>;
                            })}
                          </ol>
                        </CCol>
                      </CCol>

                      <CCol md={12} className="d-flex my-3">
                        <CCol sm={1}> </CCol>{" "}
                        <CCol sm={3}>
                          <span style={{ marginRight: "15px" }}>
                            <strong>Experience:</strong>
                          </span>{" "}
                        </CCol>{" "}
                        <CCol sm={7}> {mydata?.experience} Year</CCol>
                      </CCol>

                      <CCol md={12} className="d-flex my-3">
                        <CCol sm={1}> </CCol>{" "}
                        <CCol sm={3}>
                          <span style={{ marginRight: "15px" }}>
                            <strong>Practice State :</strong>
                          </span>{" "}
                        </CCol>{" "}
                        <CCol sm={7}>
                          <ol className="px-3" style={{ padding: "0px" }}>
                            {mydata?.practice_state?.map((items, index) => {
                              return <li key={index}>{items}</li>;
                            })}
                          </ol>
                          {/* {mydata?.practice_state} */}
                        </CCol>
                      </CCol>

                      <CCol md={12} className="d-flex my-3">
                        <CCol sm={1}> </CCol>{" "}
                        <CCol sm={3}>
                          <span style={{ marginRight: "15px" }}>
                            <strong>language :</strong>
                          </span>{" "}
                        </CCol>{" "}
                        <CCol sm={7}> {mydata?.language}</CCol>
                      </CCol>

                      <CCol md={12} className="d-flex my-3">
                        <CCol sm={1}> </CCol>{" "}
                        <CCol sm={3}>
                          <span style={{ marginRight: "15px" }}>
                            <strong>Description :</strong>
                          </span>{" "}
                        </CCol>{" "}
                        <CCol sm={7}> {mydata?.description}</CCol>
                      </CCol>


                      <CCol md={12} className="d-flex my-3">
                        <CCol sm={1}> </CCol>{" "}
                        <CCol sm={3}>
                          <span style={{ marginRight: "15px" }}>
                            <strong>Call/Chat History :</strong>
                          </span>{" "}
                        </CCol>{" "}
                        <CCol sm={7}>
                        <Link
                          
                          to={`/call-history/${id}`}
                          className="font-weight-bold font-xs text-medium-emphasis"
                        >
                          <CButton className="text-white" color="dark">
                            View 
                          </CButton>
                        </Link>
                        </CCol>
                      </CCol>

                    </CCardBody>
                  </CCard>
                </CCol>

                

                {/* Second Container */}

                <CCol md={12}>
                  <CCard className="mb-4">
                    <CCardHeader>
                      <strong>Document</strong>
                    </CCardHeader>
                    <CCardBody>
                      {mydata?.license_no == "" && mydata?.license == "" && mydata?.pan_no == "" && mydata?.pan == "" ? (
                        <div className="text-center">
                          <span style={{ color: "red" }}>
                            No Document Available
                          </span>
                        </div>
                      ) : (
                        <>
                          {" "}
                          <CCol md={12} className="d-flex my-3">
                            <CCol sm={1}> </CCol>{" "}
                            <CCol sm={3}>
                              <span style={{ marginRight: "15px" }}>
                                <strong>Bar Council ID:</strong>
                              </span>{" "}
                            </CCol>{" "}
                            <CCol sm={7}>{mydata?.license_no}</CCol>
                          </CCol>
                          <CCol md={12} className="d-flex my-3">
                            <CCol sm={1}> </CCol>{" "}
                            <CCol sm={3}>
                              <span style={{ marginRight: "15px" }}>
                                <strong>Image :</strong>
                              </span>{" "}
                            </CCol>{" "}
                            <CCol sm={7}>
                              {mydata?.license == "" ? (
                                <span style={{ color: "red" }}>No Bar ID</span>
                              ) : (
                                <Link to={mydata?.license} target="_blank">
                                  {" "}
                                  <CImage
                                    // align="center"
                                    width={75}
                                    rounded
                                    thumbnail
                                    orientation="top"
                                    src={mydata?.license}
                                  />{" "}
                                </Link>
                              )}
                            </CCol>
                          </CCol>
                          <CCol md={12} className="d-flex my-3">
                            <CCol sm={1}> </CCol>{" "}
                            <CCol sm={3}>
                              <span style={{ marginRight: "15px" }}>
                                <strong>Pan Card:</strong>
                              </span>{" "}
                            </CCol>{" "}
                            <CCol sm={7}>{mydata?.pan_no}</CCol>
                          </CCol>
                          <CCol md={12} className="d-flex my-3">
                            <CCol sm={1}> </CCol>{" "}
                            <CCol sm={3}>
                              <span style={{ marginRight: "15px" }}>
                                <strong>Image :</strong>
                              </span>{" "}
                            </CCol>{" "}
                            <CCol sm={7}>
                              {mydata?.pan == "" ? (
                                <span style={{ color: "red" }}>
                                  No Pan Card
                                </span>
                              ) : (
                                <Link to={mydata?.pan} target="_blank">
                                  {" "}
                                  <CImage
                                    // align="center"
                                    width={75}
                                    rounded
                                    thumbnail
                                    orientation="top"
                                    src={mydata?.pan}
                                  />{" "}
                                </Link>
                              )}
                            </CCol>
                          </CCol>
                        </>
                      )}
                    </CCardBody>
                  </CCard>
                </CCol>

                {/* Third Container */}
                <CCol md={12}>
                  <CCard className="mb-4">
                    <CCardHeader>
                      <strong>Bank Details</strong>
                    </CCardHeader>
                    <CCardBody>
                      {mydata?.account_name == "" && mydata?.ifsc == "" && mydata?.ac_no == "" ?  (
                        <div className="text-center">
                          <span style={{ color: "red" }}>
                            No Bank Details Available
                          </span>
                        </div>
                      ) : (
                        <>
                          {" "}
                          
                          <CCol md={12} className="d-flex my-3">
                            <CCol sm={1}> </CCol>{" "}
                            <CCol sm={3}>
                              <span style={{ marginRight: "15px" }}>
                                <strong>Account Holder's Name :</strong>
                              </span>{" "}
                            </CCol>{" "}
                            <CCol sm={7}>{mydata?.account_name}</CCol>
                          </CCol>
                          <CCol md={12} className="d-flex my-3">
                            <CCol sm={1}> </CCol>{" "}
                            <CCol sm={3}>
                              <span style={{ marginRight: "15px" }}>
                                <strong>IFSC Code :</strong>
                              </span>{" "}
                            </CCol>{" "}
                            <CCol sm={7}>{mydata?.ifsc}</CCol>
                          </CCol>
                          <CCol md={12} className="d-flex my-3">
                            <CCol sm={1}> </CCol>{" "}
                            <CCol sm={3}>
                              <span style={{ marginRight: "15px" }}>
                                <strong>Bank Account Number :</strong>
                              </span>{" "}
                            </CCol>{" "}
                            <CCol sm={7}> {mydata?.ac_no}</CCol>
                          </CCol>
                        </>
                      )}
                    </CCardBody>
                  </CCard>
                </CCol>

                {/* Fourth Container */}

                {/* <CCol md={12}>
                  <CCard className="mb-4">
                    <CCardHeader>
                      <CCol md={12} className="d-flex justify-content-between ">
                        <strong className="mt-2">Call History</strong>

                        <Link
                          // onClick={open}
                          to="/call-history"
                          className="font-weight-bold font-xs text-medium-emphasis"
                        >
                          <CButton className="text-white" color="dark">
                            View More
                          </CButton>
                        </Link>
                      </CCol>
                    </CCardHeader>
                    <CCardBody>
                      <CRow className="mb-2 px-2">
                        <CCol sm={6}>
                          <CCardBody>
                            <CCard
                              className="mb-3"
                              style={{ maxWidth: "540px" }}
                            >
                              <CRow className="g-0">
                                <CCol md={4}>
                                  <img
                                    width={80}
                                    style={{ borderRadius: "50%" }}
                                    className="m-4"
                                    src="https://cdn.vectorstock.com/i/preview-1x/32/12/default-avatar-profile-icon-vector-39013212.jpg"
                                    alt="MDN logo"
                                  />
                                </CCol>
                                <CCol md={8}>
                                  <CCardBody>
                                    <CCardTitle>Username {chatHistory?.user_name}</CCardTitle>
                                   
                                    <CCardSubtitle className="mb-2 mt-2 text-medium">
                                      Order Type: {chatHistory?.first_name + " " + mydata?.last_name}
                                    </CCardSubtitle>
                                    <CCardSubtitle className="mb-2 mt-2 text-medium">
                                      Date: {chatHistory?.first_name + " " + mydata?.last_name}
                                    </CCardSubtitle>
                                    <CCardSubtitle className="mb-2 mt-2 text-medium">
                                      Call Duration: {chatHistory?.first_name + " " + mydata?.last_name}
                                    </CCardSubtitle>
                                    <CCardSubtitle className="mb-2 mt-2 text-medium">
                                      Price/min: {chatHistory?.call_charge}
                                    </CCardSubtitle>
                                    <CCardSubtitle className="mb-2 mt-2 text-medium">
                                      Total Amount: {chatHistory?.chat_charge}
                                    </CCardSubtitle>
                                  </CCardBody>
                                </CCol>
                              </CRow>
                            </CCard>
                          </CCardBody>
                        </CCol>
                       
                      </CRow>
                    </CCardBody>
                  </CCard>
                </CCol> */}
                

                
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default ViewLawyer;
